@use 'sass:math'; /* stylelint-disable-line at-rule-no-unknown */
// Ð¿ÑÐµÐ²Ð´Ð¾Ð±Ð»Ð¾Ðº

@mixin pseudo($display: block, $pos: absolute, $content: '') {
  content: $content;
  display: $display;
  position: $pos;
}

// css-Ñ‚Ñ€ÐµÑƒÐ³Ð¾Ð»ÑŒÐ½Ð¸Ðº
@mixin css-triangle($color, $direction, $size: 6px, $position: absolute, $round: false) {
  @include pseudo($pos: $position);
  width: 0;
  height: 0;
  @if $round {
    border-radius: 3px;
  }
  @if $direction == down {
    border-left: $size solid transparent;
    border-right: $size solid transparent;
    border-top: $size solid $color;
    margin-top: 0 - round( math.div($size, 2.5) );
  }
  @else if $direction == up {
    border-left: $size solid transparent;
    border-right: $size solid transparent;
    border-bottom: $size solid $color;
    margin-bottom: 0 - round( math.div($size, 2.5) );
  }
  @else if $direction == right {
    border-top: $size solid transparent;
    border-bottom: $size solid transparent;
    border-left: $size solid $color;
    margin-right: -$size;
  }
  @else if  $direction == left {
    border-top: $size solid transparent;
    border-bottom: $size solid transparent;
    border-right: $size solid $color;
    margin-left: -$size;
  }
}

// Ð·Ð°Ñ‰Ð¸Ñ‚Ð° Ð¾ Ñ‚ÐµÐºÑÑ‚Ð¾Ð²Ð¾Ð³Ð¾ Ð¿ÐµÑ€ÐµÐ¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ñ
@mixin text-overflow() {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// Ð·Ð°Ñ‰Ð¸Ñ‚Ð° Ð¾ Ñ‚ÐµÐºÑÑ‚Ð¾Ð²Ð¾Ð³Ð¾ Ð¿ÐµÑ€ÐµÐ¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ñ (Ð¼Ð½Ð¾Ð³Ð¾ÑÑ‚Ñ€Ð¾Ñ‡Ð½Ð¾Ðµ)
@mixin paragraph-text-overflow($lines-count: 3) {
  display: -webkit-box; /* stylelint-disable-line value-no-vendor-prefix */
  overflow: hidden;
  -webkit-line-clamp: $lines-count; /* stylelint-disable-line property-no-vendor-prefix */
  -webkit-box-orient: vertical; /* stylelint-disable-line property-no-vendor-prefix */
  text-overflow: ellipsis;
}

// ÑÑ‚Ð¸Ð»Ð¸Ð·Ð°Ñ†Ð¸Ñ ÑÐºÑ€Ð¾Ð»Ð»Ð° ÑÐ»-Ñ‚Ð°
@mixin scroll-style($width: 7px, $scrollbar-color: #d1d1d1) {
  // ÑÑ‚Ð¸Ð»Ð¸Ð·Ð°Ñ†Ð¸Ñ ÑÐºÑ€Ð¾Ð»Ð»Ð°
  // Mozilla
  scrollbar-color: rgba(0, 0, 0, 0.4) $scrollbar-color;
  scrollbar-width: thin;
  // Webkit
  /* stylelint-disable-next-line */
  &::-webkit-scrollbar {
    width: $width;
    height: $width;
    background-color: $scrollbar-color;
    border-radius: 3px;
  }
  /* stylelint-disable-next-line */
  &::-webkit-scrollbar-thumb {
    border-radius: 3px;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.4);
  }
}

// Ð°Ð´Ð°Ð¿Ñ‚Ð¸Ð²Ð½Ð¾Ðµ Ð²Ð¸Ð´ÐµÐ¾ (Ð¿Ð¾ ÑƒÐ¼Ð¾Ð»Ñ‡Ð°Ð½Ð¸ÑŽ Ð¿Ñ€Ð¾Ð¿Ð¾Ñ€Ñ†Ð¸Ð¸ Ð´Ð»Ñ Ð²Ð¸Ð´ÐµÐ¾ Ñ YouTube)
@mixin adaptive-video($proportion: 56.25%, $bg-color: #000) {
  position: relative;
  width: auto;
  height: 0;
  padding-bottom: $proportion;
  background-color: $bg-color;

  /* stylelint-disable */
  iframe,
  object,
  embed,
  video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
  }
  /* stylelint-enable */
}

// Ñ‚ÐµÐºÑÑ‚ Ñ Ð³Ñ€Ð°Ð´Ð¸ÐµÐ½Ñ‚Ð¾Ð¼
@mixin gradient-text($gradient) {
  background: $gradient;
  -webkit-background-clip: text; /* stylelint-disable-line property-no-vendor-prefix */
  -webkit-text-fill-color: transparent; /* stylelint-disable-line property-no-vendor-prefix */
}


// Ñ„Ð¸Ð³ÑƒÑ€Ð½Ñ‹Ð¹ Ñ„Ð¾Ð½ Ð±Ð»Ð¾ÐºÐ¾Ð²
@mixin polygonBackground($polygon, $color, $zIndex: -1, $position: relative) {
  position: $position;

  /* stylelint-disable-next-line */
  &::before {
    @include pseudo();
    z-index: $zIndex;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $color;
    clip-path: $polygon;
  }
}

// Ð¿Ñ€Ð¸Ð¼Ð¸Ñ‚Ð¸Ð²Ð½Ð½Ð°Ñ Ð°Ð½Ð¸Ð¼Ð°Ñ†Ð¸Ñ Ð½Ð° hover
@mixin hover($delay: 0.25s) {
  transition: opacity $delay ease-in;

  /* stylelint-disable-next-line plugin/selector-bem-pattern */
  &:hover {
    opacity: 0.65;
  }
}

@mixin hoverImg {
  @include hover();

  /* stylelint-disable-next-line plugin/selector-bem-pattern */
  &:hover {
    overflow: hidden;

    & img { /* stylelint-disable-line plugin/selector-bem-pattern */
      transform: scale(1.025);
      transition: transform 0.25s ease-in;
    }
  }
}

@mixin titleStyle {
  color: #000000;
  font-weight: 600;
  font-size: 24px;
  line-height: 80px;
  text-align: center;

  @media screen and (min-width: $screen-xs) {
    font-size: 30px;
    line-height: 80px;
  }

  @media screen and (min-width: $screen-md) {
    font-size: 36px;
    line-height: 80px;
  }

  @media screen and (min-width: $screen-lg) {
    font-size: 48px;
    line-height: 80px;
  }
}
